body{
    a{
    text-decoration: none;

    
    }
    .login-container{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        max-width: 350px;
        max-height: 250px;
        background-color: rgb(255 , 255, 255);

        .form-control{
            background-color: rgb(255, 255, 255);
        }
    }

    .pages-container{
        margin-top: 55px;
        height: calc(100vh - 55px);
        overflow: auto;

        .cancel{
            background-color: rgb(253, 186, 186) !important;
        }
        .completed{
            background-color: rgb(186, 253, 203) !important;
        }
        .pending{
            background-color: rgb(253, 224, 186) !important;
        }
        .inprocess{
            
            background-color: rgb(246, 253, 186) !important;
        }
        
          
          }
          
    

}


.fieldset{
    // margin: 10px;
    padding :10px 10px;
    border-radius: 5px;
    border: 1px solid #868686;
    align-items: center;

    .legend{
        position: relative;
        padding: 0px 10px;
        top: -25px;
        left: 5px;
        font-size: 18px;
        font-weight: bold;
        text-align: center;
        border-radius: 5px 5px 0px 0px;
        background-color: #FFFFFF;
        
      }
}

.active{
    color: #ffffff;
}

.dropdown-item.active{
    background-color: #212529 !important;
}

